/* classes for sortable table headers --------------------------------------- */
tr.sortable {
    th.sortasc,
    th.sortdesc {
        .tablesorter-header-inner {
            display: inline-block;
        }
    }

    th.sortasc {
        .icon('after', 'arr_1up');
    }
    th.sortdesc {
        .icon('after', 'arr_1down');
    }
}

.tablesorter .filtered {
    display: none;
}

.tablesorter .tablesorter-errorRow td {
    text-align: center;
    cursor: pointer;
}

/* styles for settings tables */
.settings {
    border-collapse: collapse;
    margin-bottom: 2em;
    width: 100%;

    thead th, tbody th {
        .table_header_bold;
        text-align: center;
    }
    td, th {
        padding: 8px;
        vertical-align: top;
    }
    tbody {
        &.maxed {
            input[type=email], input[type=password], input[type=tel], input[type=text], input[type=url], select, textarea {
                &:first-child {
                    box-sizing: border-box;
                    width: 100%;
                }
            }
            td[colspan]:first-child {
                input[type=email], input[type=password], input[type=tel], input[type=text], input[type=url], select, textarea {
                    width: 200px;
                }
            }
        }
        &.privacy td:first-child ~ td {
            font-style: italic;
            text-align: center;
        }
        td:first-child label {
            display: block;
            font-weight: bold;
        }
    }
    td:first-child[colspan], .divider > th, .divider > td {
        background-color: lighten(@brand-color-lighter, 20%);
        border-bottom: 1px solid #444;
        border-top: 1px solid #444;
        color: #000;
        font-weight: bold;
        text-align: center;
    }

    dfn, small {
        display: block;
        font-weight: normal;
    }
    dfn {
        font-size: 0.8em;
        font-style: italic;
        padding-top: 0.5em;
    }
    tfoot {
        td {
            background: @table-footer-color;
            text-align: center;
        }
        tr:first-child td {
            border-top: 1px solid #575757;
        }
    }
    label.required:after {
        color: red;
        content:'*';
        font-size: 1.5em;
        padding-left: 5px;
        vertical-align: middle;
    }
    &.notification tbody td {
        text-align: center;
        &:first-child:not([colspan]) { padding-left: 0; padding-right: 0; font-size: small; }
        &:nth-child(-n+2) { text-align: left; }
    }
    .bordered {
        &.left  { border-left: 1px solid @brand-color-lighter; }
        &.right { border-right: 1px solid @brand-color-lighter; }
    }
}

table.tree {
    .header > td {
        .gradient-bar();

        a.link {
            padding-left: 5px;
            &.open   {
                .background-icon('arr_1down', 'clickable');
                background-position: left center;
                background-repeat: no-repeat;
                padding-left: 20px;
            }
            &.closed {
                .background-icon('arr_1right', 'clickable');
                background-position: left center;
                background-repeat: no-repeat;
                padding-left: 20px;
            }
        }
    }
    td.blank {
        background: #fff;
        border: 0;
    }
    td.in-between {
        background: #fff url("@{image-path}/tree-line.gif") center repeat-y;
        border: 0;
    }
    td.leaf {
        background: #fff url("@{image-path}/tree-leaf.gif") center no-repeat;
        border: 0;
    }
    td.end {
        background: #fff url("@{image-path}/tree-end.gif") center no-repeat;
        border: 0;
    }
    .centered {
        text-align: center;
        table { margin: auto; text-align: left; }
    }
}

.table_footer, .table_footer td {
    background-color: #e9e9e9;
    border-top: 1px solid #c8c8c8;
}

// New table definition
table.default {
    border-collapse: collapse;
    margin-bottom: 1em;
    width: 100%;

    .wrap-content {
        word-break: break-all;
    }

    .font-size-adjusted{
        font-size: 1.1em;
    }

    th, td, caption {
        padding: 5px;
        text-align: left;
    }
    > caption {
        background-color: transparent;
        padding-top: 0px;
        color: @base-gray;
        font-size: 1.4em;
        text-align: left;
        margin-top: 4px;

        header {
            > h2 {
                border: 0;
                font-size: inherit;
                font-weight: normal;
                margin: 0;
                padding: 0;
            }
            > p {
                font-size: 0.7em;
                font-weight: normal;
                margin: 0;
                padding: 0;
            }
        }
    }
    > thead {
        > tr > th {
            background-color: @content-color-20;
            border-bottom: 1px solid fadeout(@brand-color-lighter, 80%);
            border-top: 1px solid @brand-color-darker;
            font-size: 1.0em;
        }
    }
    > tbody {
        > tr {
            > th {
                background-color: @content-color-20;
                border-top: 1px solid @brand-color-darker;
                border-bottom: 1px solid fadeout(@brand-color-lighter, 80%);
                text-align: left;
            }
            > td {
                border-bottom: 1px solid @table-header-color;
                transition: background-color 0.3s;
            }
            > td.dragHandle {
                background: #ffffff url("@{image-path}/anfasser_24.png") center no-repeat;
                cursor: move;
            }
            &.dragover > td {
                background-color: @yellow-20;
            }
        }
    }
    > tbody > tr.new > td {
        font-weight: bold;
        &:first-child {
            position: relative;
            &::before {
                display: block;
                content: '';
                position: absolute;

                top: 0;
                bottom: 0;
                left: 0;
                width: 2px;
                background-color: @red;
            }
        }
        .action-menu {
            font-weight: normal;
        }
    }
    > tbody:last-of-type > tr:last-child > td {
        > table > tbody > tr > td {
            border-bottom: 1px solid @table-header-color;
        }
        border-bottom: 1px solid @brand-color-darker;
    }
    // Hover effect
    &:not(.nohover) > tbody:not(.nohover) > tr:not(.nohover):hover > td:not(.nohover) {
        background-color: fadeout(@light-gray-color, 80%);
    }
    > tfoot {
        > tr > td {
            background-color: @content-color-20;
            border-top: 1px solid @brand-color-darker;
            padding-left: 10px;
            padding-right: 10px;
        }
    }
    td.avatar, th.avatar {
        padding: 5px;
    }
    .actions {
        float: right;
        text-align: right;
        white-space: nowrap;
        img, svg, input[type="image"] {
            vertical-align: middle;
        }
    }
    > caption .actions {
        font-size: @font-size-base;
        border-left: 1px solid @brand-color-darker;
        margin-bottom: -5px;
        padding-bottom: 7px;
        padding-left: 0.5em;
    }
    td.actions, th.actions {
        float: none;
    }

    > caption {
        .caption-container {
            display: flex;
            align-items: stretch;
            justify-content: space-between;
            margin-bottom: -5px;
        }
        .caption-content {
            flex-grow: 1;
            border-right: 1px solid @brand-color-darker;
            padding-bottom: 5px;
            padding-right: 0.5em;
            margin-right: 0.5em;
        }
        .caption-actions {
            align-self: flex-end;
        }
    }

    > tbody.toggleable {
        &.toggled {
            .toggle-switch {
                .background-icon('arr_1right', 'clickable');
            }
            tr:not(:first-child) {
                display: none;
            }
        }
        .toggle-switch {
            .hide-text();
            .background-icon('arr_1down', 'clickable');
            display: inline-block;
            height: 16px;
            text-align: center;
            vertical-align: top;
            width: 16px;
        }
    }

    dfn, small {
        display: block;
        font-weight: normal;
    }
    dfn {
        font-size: 0.8em;
        font-style: italic;
        padding-top: 0.5em;
    }
    label.required:after {
        color: red;
        content:'*';
        font-size: 1.5em;
        padding-left: 5px;
        vertical-align: middle;
    }

    &.has-form {
        input[type=text], textarea {
            box-sizing: border-box;
            min-width: 200px;
            width: 100%;
        }
        textarea {
            min-height: 100px;
        }
    }

    tfoot {
        // Fix button and select alignment
        select {
            vertical-align: middle;
        }
        // Adjust button margins
        .button {
            margin-bottom: 0;
            margin-top: 0;
        }
    }

    colgroup {
        col.checkbox {
            width: 24px;
        }
    }
}


// Remove trailing border and margin in content boxes if table is last element
article.studip > section > table.default:last-child {
    margin-bottom: 0;

    > tbody:last-child > tr:last-child > td {
        border-bottom: 0;
    }
}

table.withdetails {
    > tbody > tr:not(.details) > td:first-child {
        .background-icon('arr_1right', 'clickable');
        background-repeat: no-repeat;
        background-position: 2px center;
        padding-left: 20px;
        > a {
            margin-left: -20px;
            padding-left: 20px;
        }
    }
    > tbody > tr.open > td {
        background-color: fadeout(@light-gray-color, 80%);
    }
    > tbody > tr.open > td:first-child {
        .background-icon('arr_1down', 'clickable');
    }
    tr.details {
        display: none;
        max-height: 0px;
        overflow: hidden;
        transition: max-height 0.8s;
    }
    tr.open + tr.details {
        display: table-row;
        max-height: 200px;
        overflow: hidden;
        transition: max-height 0.8s;
        background-color: transparent !important;
        > td {
            padding-top: 0px;
            padding-bottom: 10px;
            > .detailscontainer {
                padding: 5px;
                border: 1px solid @table-header-color;
                margin-top: -1px;
                border-top-color: white;
            }
        }
    }
}
.no-js table.withdetails tr.details {
    display: table-row;
}

.sortable-dreieck(@direction) {
    &::after {
        background-repeat: no-repeat;
        content: ' ';
        display: inline-block;
        height: 16px;
        margin-left: 0;
        .retina-background-image('dreieck_@{direction}.png', 'dreieck_@{direction}@2x.png', 16px, 16px);
        vertical-align: text-top;
        width: 16px;
    }
}

.sortable-table {
    .header, .tablesorter-header:not(.sorter-false) {
        white-space: nowrap;

        color: @base-color;
        &:hover {
            color: @active-color;
            cursor: pointer;
        }
    }
    .headerSortUp, .tablesorter-headerDesc .tablesorter-header-inner {
        .sortable-dreieck('down');
        &::after {
            vertical-align: inherit;
        }
    }
    .headerSortDown, .tablesorter-headerAsc .tablesorter-header-inner {
        .sortable-dreieck('up');
        &::after {
            vertical-align: inherit;
        }
    }
    .tablesorter-headerUnSorted:not(.sorter-false) .tablesorter-header-inner {
        margin-right: 15px;
    }
}

// Schedule table
table#schedule_data {
    width: 100%;
    table-layout: fixed;
    thead {
        tr {
            td {
                text-align: center;
                vertical-align: top;
                background-color: @content-color-20;
                padding-right: 2px;
                padding: 0px;
                &:first-child {
                    width: 40px;
                }
            }
            &:first-child {
                td:first-child {
                    background-color: inherit;
                }
            }
        }
    }
    tbody {
        td:first-child {
            text-align: right;
            vertical-align: top;
            background-color: @content-color-20;
            padding-right: 2px;
            padding: 0px;
        }
    }
}

